import React, { useState, useEffect } from 'react';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '../ui/table';
import { Button } from '../ui/button';
import { Input } from '../ui/input';
import { Alert, AlertTitle, AlertDescription } from '../ui/alert';
import { Card, CardHeader, CardTitle, CardContent, CardDescription } from '../ui/card';
import { Save, AlertCircle, CheckCircle2, Loader2, Info, RefreshCw, ArrowLeft, Globe } from 'lucide-react';
import { 
  getCurrentMappings,
  updateMappings,
  resetMappings,
  getFieldGroups,
  getDisplayName,
  getAlternativeNames,
  clearMappingsCache
} from './columnMappingService';
import { Tabs, TabsList, TabsTrigger, TabsContent } from '../ui/tabs';
import { CSV_TYPES, CSV_TYPE_DISPLAY_NAMES, OVERVIEW_FIELDS, OVERVIEW_FIELDS_ENGLISH, VIDEO_FIELDS, VIDEO_FIELDS_ENGLISH } from '@/utils/constants';

/**
 * Komponent fr att redigera kolumnmappningar fr CSV-filer
 * @param {Object} props - Komponentens properties
 * @param {Function} props.onBack - Callback fr att g tillbaka
 */
export function ColumnMappingEditor({ onBack }) {
  const [csvType, setCsvType] = useState(CSV_TYPES.OVERVIEW);
  const [mappings, setMappings] = useState({});
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [isResetting, setIsResetting] = useState(false);
  const [showExamples, setShowExamples] = useState({});

  // Ladda mappningar nr csv-typ ndras
  useEffect(() => {
    loadMappings();
  }, [csvType]);

  const loadMappings = async () => {
    console.log(`ColumnMappingEditor: Brjar ladda mappningar fr ${csvType}`);
    setIsLoading(true);
    
    try {
      const data = await getCurrentMappings(csvType);
      console.log(`ColumnMappingEditor: Laddade mappningar fr ${csvType}:`, data);
      setMappings(data);
      setError(null);
    } catch (err) {
      console.error(`ColumnMappingEditor: Fel vid laddning fr ${csvType}:`, err);
      setError('Kunde inte ladda kolumnmappningar: ' + err.message);
    } finally {
      setIsLoading(false);
      console.log(`ColumnMappingEditor: Laddning slutfrd fr ${csvType}`);
    }
  };

  const handleSave = async () => {
    console.log(`ColumnMappingEditor: Brjar spara ndringar fr ${csvType}`);
    console.log('ColumnMappingEditor: Mappningar att spara:', mappings);
    
    setIsSaving(true);
    setError(null);
    
    try {
      // Spara mappningarna
      await updateMappings(csvType, mappings);
      console.log('ColumnMappingEditor: Sparning lyckades');
      
      // Rensa cachen fr att skerstlla att alla komponenter fr de nya mappningarna
      clearMappingsCache();
      
      // Visa framgngsmeddelande
      setSuccessMessage('ndringarna har sparats.');
      setSuccess(true);
      setTimeout(() => {
        setSuccess(false);
      }, 5000);
    } catch (err) {
      console.error('ColumnMappingEditor: Fel vid sparning:', err);
      setError('Kunde inte spara ndringarna: ' + err.message);
    } finally {
      setIsSaving(false);
    }
  };

  const handleReset = async () => {
    console.log(`ColumnMappingEditor: terstller till standardvrden fr ${csvType}`);
    
    setIsResetting(true);
    setError(null);
    
    try {
      // terstll mappningar till standard
      const defaultMappings = await resetMappings(csvType);
      setMappings(defaultMappings);
      console.log('ColumnMappingEditor: terstllning lyckades');
      
      // Rensa cachen fr att skerstlla att alla komponenter fr de nya mappningarna
      clearMappingsCache();
      
      // Visa framgngsmeddelande
      setSuccessMessage('Mappningarna har terstllts till standardvrden.');
      setSuccess(true);
      setTimeout(() => {
        setSuccess(false);
      }, 5000);
    } catch (err) {
      console.error('ColumnMappingEditor: Fel vid terstllning:', err);
      setError('Kunde inte terstlla mappningarna: ' + err.message);
    } finally {
      setIsResetting(false);
    }
  };

  // Hantera vrdendringar i input-flt
  const handleValueChange = (originalName, internalName, newValue, language) => {
    if (!newValue.trim()) {
      setError('Kolumnnamn kan inte vara tomt');
      return;
    }
    
    console.log('ColumnMappingEditor: ndrar mappning');
    console.log('Frn:', originalName);
    console.log('Till:', newValue);
    console.log('Sprk:', language);
    
    // Kontrollera om det nya vrdet redan finns som en nyckel fr ett annat flt
    const existingMapping = Object.entries(mappings).find(([key, value]) => 
      key !== originalName && key === newValue && value !== internalName
    );
    
    if (existingMapping) {
      setError(`Kolumnnamnet "${newValue}" anvnds redan fr fltet "${existingMapping[1]}". Vlj ett annat namn.`);
      return;
    }
    
    setMappings(prev => {
      // Skapa en kopia av mappningarna
      const newMappings = { ...prev };
      
      // Om vi tar bort en befintlig mappning, men ser till att det finns en annan mappning fr samma interna flt
      if (originalName && originalName !== newValue) {
        // Ta bort den gamla mappningen
        delete newMappings[originalName];
      }
      
      // Lgg till den nya mappningen
      if (newValue && newValue.trim() !== '') {
        newMappings[newValue] = internalName;
      }
      
      console.log('ColumnMappingEditor: Nya mappningar:', newMappings);
      return newMappings;
    });
    
    // Rensa eventuellt tidigare fel
    setError(null);
  };

  // Visa/dlj exempel fr ett flt
  const toggleExamples = (internalName) => {
    setShowExamples(prev => ({
      ...prev,
      [internalName]: !prev[internalName]
    }));
  };
  
  // Hantera klick p ett exempel fr att kopiera det
  const handleExampleClick = (exampleName, internalName, language) => {
    // Hitta befintlig mappning fr detta interna flt och sprk
    let existingName = null;
    
    if (language === 'sv') {
      // Leta efter en svensk mappning
      for (const [key, value] of Object.entries(mappings)) {
        if (value === internalName && OVERVIEW_FIELDS[internalName] === key || VIDEO_FIELDS[internalName] === key) {
          existingName = key;
          break;
        }
      }
    } else {
      // Leta efter en engelsk mappning
      for (const [key, value] of Object.entries(mappings)) {
        if (value === internalName && OVERVIEW_FIELDS_ENGLISH[internalName] === key || VIDEO_FIELDS_ENGLISH[internalName] === key) {
          existingName = key;
          break;
        }
      }
    }
    
    handleValueChange(existingName, internalName, exampleName, language);
  };

  // Hmta alla mappningar fr ett internt fltnamn
  const getMappingsForInternalName = (internalName) => {
    return Object.entries(mappings)
      .filter(([_, value]) => value === internalName)
      .map(([key, _]) => key);
  };

  // Hmta den svenska mappningen fr ett internt flt
  const getSwedishMapping = (internalName) => {
    // Standardmappning frn konstanter
    const defaultMapping = csvType === CSV_TYPES.OVERVIEW 
      ? OVERVIEW_FIELDS[internalName] 
      : VIDEO_FIELDS[internalName];
    
    // Leta efter en befintlig svensk mappning i mappings
    for (const [key, value] of Object.entries(mappings)) {
      if (value === internalName) {
        // Om nyckeln matchar standardmappningen eller innehller svenska tecken (, , )
        if (key === defaultMapping || /[]/i.test(key)) {
          return key;
        }
      }
    }
    
    // Om ingen hittades, returnera standardmappningen
    return defaultMapping;
  };

  // Hmta den engelska mappningen fr ett internt flt
  const getEnglishMapping = (internalName) => {
    // Standardmappning frn konstanter
    const defaultMapping = csvType === CSV_TYPES.OVERVIEW 
      ? OVERVIEW_FIELDS_ENGLISH[internalName] 
      : VIDEO_FIELDS_ENGLISH[internalName];
    
    // Leta efter en befintlig engelsk mappning i mappings
    for (const [key, value] of Object.entries(mappings)) {
      if (value === internalName) {
        // Om nyckeln matchar standardmappningen eller inte innehller svenska tecken (, , )
        if (key === defaultMapping || (!/[]/i.test(key) && key !== OVERVIEW_FIELDS[internalName] && key !== VIDEO_FIELDS[internalName])) {
          return key;
        }
      }
    }
    
    // Om ingen hittades, returnera standardmappningen
    return defaultMapping;
  };

  // Visa laddningsindikator
  if (isLoading) {
    return (
      <Card>
        <CardContent className="p-6">
          <div className="flex items-center justify-center space-x-2">
            <Loader2 className="h-4 w-4 animate-spin" />
            <p>Laddar kolumnmappningar...</p>
          </div>
        </CardContent>
      </Card>
    );
  }

  // Hmta fltgrupper fr vald CSV-typ
  const fieldGroups = getFieldGroups(csvType);

  return (
    <Card>
      <CardHeader>
        <div className="flex justify-between items-center">
          <CardTitle>Hantera kolumnmappningar</CardTitle>
          {onBack && (
            <Button 
              variant="ghost" 
              size="sm" 
              onClick={onBack}
            >
              <ArrowLeft className="h-4 w-4 mr-1" />
              Tillbaka
            </Button>
          )}
        </div>
        <CardDescription>
          Nr TikTok ndrar kolumnnamn i exportfilerna behver du uppdatera mappningarna hr.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-6">
          {/* CSV-typsvljare */}
          <Tabs 
            value={csvType} 
            onValueChange={setCsvType}
            className="w-full"
          >
            <TabsList className="grid w-full grid-cols-2">
              <TabsTrigger value={CSV_TYPES.OVERVIEW}>
                {CSV_TYPE_DISPLAY_NAMES[CSV_TYPES.OVERVIEW]}
              </TabsTrigger>
              <TabsTrigger value={CSV_TYPES.VIDEO}>
                {CSV_TYPE_DISPLAY_NAMES[CSV_TYPES.VIDEO]}
              </TabsTrigger>
            </TabsList>
          </Tabs>

          {/* Felmeddelande */}
          {error && (
            <Alert variant="destructive" className="animate-in fade-in duration-200">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>Fel</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Framgngsmeddelande */}
          {success && (
            <Alert className="bg-green-50 border-green-200 animate-in fade-in duration-200">
              <CheckCircle2 className="h-4 w-4 text-green-600" />
              <AlertTitle className="text-green-800">ndringar sparade</AlertTitle>
              <AlertDescription className="text-green-700">
                <div className="space-y-2">
                  <p>{successMessage}</p>
                  <p className="font-semibold">Du mste ladda in en ny CSV-fil fr att ndringarna ska brja glla.</p>
                </div>
              </AlertDescription>
            </Alert>
          )}

          {/* Instruktioner */}
          <div className="bg-muted/40 p-4 rounded-md border text-sm">
            <h4 className="font-medium mb-2">S hr uppdaterar du mappningar:</h4>
            <ol className="list-decimal pl-5 space-y-1">
              <li>Ladda upp en ny CSV-fil frn TikTok</li>
              <li>Om filen inte kan lsas in, notera vilka kolumner som saknas</li>
              <li>Hitta kolumnen med det gamla namnet under <strong>Svenska</strong> eller <strong>Engelska</strong> och ndra det till det nya namnet som TikTok nu anvnder</li>
              <li>Klicka p <strong>Spara ndringar</strong></li>
              <li>Ladda upp CSV-filen igen</li>
            </ol>
            <div className="mt-2 text-blue-600 font-medium flex items-center">
              <Globe className="h-4 w-4 mr-1" />
              <span>
                Uppdatera bde svenska och engelska kolumnnamn fr bsta kompatibilitet.
              </span>
            </div>
          </div>

          {/* Mappningstabeller per kategori */}
          {Object.entries(fieldGroups).map(([groupName, internalNames]) => (
            <div key={groupName}>
              <h3 className="text-lg font-semibold mb-2">{groupName}</h3>
              <div className="rounded-md border mb-4">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead className="w-1/4">Fltnamn</TableHead>
                      <TableHead className="w-1/3">Svenska kolumnnamn</TableHead>
                      <TableHead className="w-1/3">Engelska kolumnnamn</TableHead>
                      <TableHead className="w-10"></TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {internalNames.map(internalName => {
                      const swedishMapping = getSwedishMapping(internalName);
                      const englishMapping = getEnglishMapping(internalName);
                      
                      return (
                        <React.Fragment key={internalName}>
                          <TableRow>
                            <TableCell className="font-medium">
                              {getDisplayName(internalName, csvType)}
                            </TableCell>
                            {/* Svenska mappning */}
                            <TableCell>
                              <Input
                                value={swedishMapping || ''}
                                onChange={(e) => handleValueChange(swedishMapping, internalName, e.target.value, 'sv')}
                                className="max-w-sm"
                                disabled={isSaving || isResetting}
                              />
                            </TableCell>
                            {/* Engelska mappning */}
                            <TableCell>
                              <Input
                                value={englishMapping || ''}
                                onChange={(e) => handleValueChange(englishMapping, internalName, e.target.value, 'en')}
                                className="max-w-sm"
                                disabled={isSaving || isResetting}
                              />
                            </TableCell>
                            <TableCell>
                              <Button 
                                variant="ghost" 
                                size="sm"
                                onClick={() => toggleExamples(internalName)}
                                title="Visa exempel p vanliga kolumnnamn"
                              >
                                <Info className="h-4 w-4" />
                              </Button>
                            </TableCell>
                          </TableRow>
                          
                          {/* Visa exempel p mjliga kolumnnamn */}
                          {showExamples[internalName] && (
                            <TableRow>
                              <TableCell colSpan={4} className="bg-slate-50">
                                <div className="p-2 text-sm">
                                  <p className="font-medium mb-1">Vanliga kolumnnamn fr detta flt:</p>
                                  
                                  <div className="space-y-2">
                                    {/* Svenska exempel */}
                                    <div>
                                      <p className="text-xs font-medium text-muted-foreground">Svenska:</p>
                                      <div className="flex flex-wrap gap-1 pl-2 text-gray-600">
                                        {getAlternativeNames(internalName, mappings)
                                          .filter(name => /[]/i.test(name) || name === OVERVIEW_FIELDS[internalName] || name === VIDEO_FIELDS[internalName])
                                          .map((name, i) => (
                                          <button
                                            key={i} 
                                            className="px-2 py-1 rounded hover:bg-blue-100 hover:text-blue-600 border border-gray-200"
                                            onClick={() => handleExampleClick(name, internalName, 'sv')}
                                          >
                                            {name}
                                          </button>
                                        ))}
                                      </div>
                                    </div>
                                    
                                    {/* Engelska exempel */}
                                    <div>
                                      <p className="text-xs font-medium text-muted-foreground">Engelska:</p>
                                      <div className="flex flex-wrap gap-1 pl-2 text-gray-600">
                                        {getAlternativeNames(internalName, mappings)
                                          .filter(name => !/[]/i.test(name) && name !== OVERVIEW_FIELDS[internalName] && name !== VIDEO_FIELDS[internalName])
                                          .map((name, i) => (
                                          <button
                                            key={i} 
                                            className="px-2 py-1 rounded hover:bg-blue-100 hover:text-blue-600 border border-gray-200"
                                            onClick={() => handleExampleClick(name, internalName, 'en')}
                                          >
                                            {name}
                                          </button>
                                        ))}
                                      </div>
                                    </div>
                                  </div>
                                  
                                  <p className="mt-2 text-xs text-gray-500">
                                    Klicka p ngot av namnen ovan fr att anvnda det.
                                  </p>
                                </div>
                              </TableCell>
                            </TableRow>
                          )}
                        </React.Fragment>
                      );
                    })}
                  </TableBody>
                </Table>
              </div>
            </div>
          ))}

          <div className="flex justify-between">
            <Button 
              onClick={handleReset} 
              disabled={isResetting || isSaving}
              variant="outline"
              className="min-w-[100px]"
            >
              {isResetting ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  terstller...
                </>
              ) : (
                <>
                  <RefreshCw className="mr-2 h-4 w-4" />
                  terstll till standard
                </>
              )}
            </Button>

            <Button 
              onClick={handleSave} 
              disabled={isSaving || isResetting}
              className="min-w-[100px]"
            >
              {isSaving ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Sparar...
                </>
              ) : (
                <>
                  <Save className="mr-2 h-4 w-4" />
                  Spara ndringar
                </>
              )}
            </Button>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}