import { useState, useEffect, useCallback } from 'react';
import { getStorageStats } from '@/utils/webStorageService';
import { STORAGE_LIMITS } from '@/utils/constants';

/**
 * Hook fr att vervaka lagringsutrymme
 * @returns {Object} Data och funktioner fr lagringsvervakning
 */
export function useStorageMonitor() {
  const [storageStats, setStorageStats] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // Hmta lagringsstatistik
  const fetchStorageStats = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);
      
      const stats = await getStorageStats();
      setStorageStats(stats);
    } catch (err) {
      console.error('Fel vid hmtning av lagringsstatistik:', err);
      setError('Kunde inte hmta lagringsstatistik');
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Uppdatera lagringsstatistik nr komponenten monteras
  useEffect(() => {
    fetchStorageStats();
  }, [fetchStorageStats]);

  // Berkna lagringsstatus
  const getStorageStatus = useCallback(() => {
    if (!storageStats) return 'unknown';
    
    const { localStorage, total } = storageStats;
    
    // Kontrollera localStorage (som har hrdare begrnsningar)
    if (localStorage.percentage > 90) {
      return 'critical';
    }
    
    if (localStorage.percentage > 75) {
      return 'warning';
    }
    
    // Kontrollera total lagring
    if (total.percentage > STORAGE_LIMITS.INDEXED_DB_WARNING / 1024 / 1024 * 100) {
      return 'warning';
    }
    
    return 'ok';
  }, [storageStats]);

  // F en beskrivande text om lagringsstatus
  const getStatusDescription = useCallback(() => {
    if (!storageStats) return 'Hmtar lagringsinformation...';
    
    const { localStorage, total } = storageStats;
    const status = getStorageStatus();
    
    if (status === 'critical') {
      return 'Lagringsutrymmet r nstan fullt. Radera data fr att fortstta anvnda appen.';
    }
    
    if (status === 'warning') {
      return 'Lagringsutrymmet brjar bli fullt. vervg att radera ondig data.';
    }
    
    // Formatera storlekar till lsbara enheter
    const formatSize = (bytes) => {
      const kb = bytes / 1024;
      if (kb < 1000) return `${kb.toFixed(1)} KB`;
      return `${(kb / 1024).toFixed(1)} MB`;
    };
    
    return `Anvnder ${formatSize(total.used)} av tillgngligt lagringsutrymme.`;
  }, [storageStats, getStorageStatus]);

  return {
    storageStats,
    isLoading,
    error,
    fetchStorageStats,
    getStorageStatus,
    getStatusDescription
  };
}